@tailwind base;
@tailwind components;
@tailwind utilities;
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@700;800;900&display=swap');

html {
  scroll-behavior: smooth;
}

body {
  @apply bg-white text-gray-900 font-sans;
}

code {
  @apply font-mono;
}

:root {
  --google-sans: 'Inter', ui-sans-serif, system-ui, -apple-system, 'Segoe UI', Roboto, Helvetica, Arial, 'Apple Color Emoji', 'Segoe UI Emoji';
}

.font-bold,
.font-extrabold,
.font-black,
b,
strong,
h1,
h2,
h3 {
  font-family: var(--google-sans);
  letter-spacing: -0.02em;
}
